

































import AppModal from '@/components/AppModal.vue'
import AppButton from '@/components/AppButton.vue'
import AppTextField from '@/components/AppTextField.vue'
import { createComponent, ref, computed } from '@vue/composition-api'
import { uploadImages } from '../api'

export default createComponent({
  name: 'FilterModal',
  components: {
    AppModal,
    AppButton,
    AppTextField
  },
  props: {
    open: {
      type: Boolean,
      default: false
    },
    dismiss: {
      type: Function,
      required: true
    },
    images: {
      type: FileList
    },
    finishImagesUpload: {
      type: Function,
      required: true
    },
    finishUploaderForm: {
      type: Function,
      required: true
    }
  },
  setup (props) {
    const nickname = ref('')
    const contact = ref('')

    const imageUrls = computed(() => {
      const urls = []
      // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
      for (let i = 0; i < props.images!.length; i++) {
        // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
        urls.push(URL.createObjectURL(props.images![i]))
      }

      return urls
    })

    return {
      nickname,
      contact,
      imageUrls,
      async handleImagesUpload () {
        // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
        const images = await uploadImages(props.images!)
        props.finishImagesUpload(images)

        props.finishUploaderForm(nickname.value, contact.value)

        // TODO: decorate dismiss method, clear images when dismiss
        props.dismiss()
      }
    }
  }
})
